___CSS_LOADER_EXPORT___.push([module.id, "\n.Vue-Toastification__toast {\n    width: 388px;\n    box-shadow: 2px 2px 15px #00000066;\n    border-radius: 4px;\n    opacity: 1;\n    color: black;\n    padding: 0px;\n}\n.Vue-Toastification__toast--warning {\n    background: #f3ca40 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--success {\n    background: #29C660 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--error {\n    background: #FF6B6B 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--info {\n    color: white;\n    background: #151233 0% 0% no-repeat padding-box;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/notification.vue"],"names":[],"mappings":";AAwMC;IACG,YAAY;IACZ,kCAAkC;IAClC,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,YAAY;AAChB;AACA;IACI,+CAA+C;AACnD;AACA;IACI,+CAA+C;AACnD;AACA;IACI,+CAA+C;AACnD;AACA;IACI,YAAY;IACZ,+CAA+C;AACnD","sourcesContent":["<script setup lang=\"ts\">\nimport { ref } from \"vue\";\nimport \"vue-toastification/dist/index.css\";\nimport { container } from \"../../../domain/core/diContainer\";\nimport { INotificationInteractor, INotificationOptions } from \"../../../domain/model/INotificationOptions\";\nimport { INotification } from \"../../../domain/presentation/INotification\";\n\nexport interface INot {\n    options: INotificationOptions;\n    zindex: number;\n}\nconst emit = defineEmits([\"close-toast\"]);\nconst props = defineProps<INot>();\nlet showDetail = ref(false);\nconst iconClass = ref(getIconClass());\n\nif (props.options?.copy) {\n    const options = props.options;\n    options.interactors = options.interactors || [];\n    options.interactors.unshift({\n        customClose: true,\n        text: \"copy\",\n        onInteract: () => {\n            const copyText = (options.title ? options.title + \"\\n\" : \"\") + options.text;\n            navigator.clipboard.writeText(copyText);\n            container.resolve<INotification>(INotification).showNotification({ text: \"copied\", type: \"success\", copy: false });\n        },\n    });\n}\nprops.options.interactors\n    ?.filter((interactor) => interactor.optionsPleaseSelect)\n    .forEach((interactor) => {\n        interactor.options = interactor.options || [];\n        if (interactor.options.find((option) => option.selected)) {\n            return;\n        }\n\n        interactor.options.unshift({ id: \"pleaseSelect\", text: \"pleaseSelect\" });\n    });\nconst requireds = {\n    interactors: props.options.interactors?.filter((interactor) => interactor.required).length || 0,\n    interracted: 0,\n};\nconst showBlockShadow = ref(requireds.interactors > 0);\n\nfunction decideClose(interactor: INotificationInteractor) {\n    if (!interactor.required) {\n        return;\n    }\n    if (requireds.interactors > ++requireds.interracted) {\n        return;\n    }\n\n    showBlockShadow.value = false;\n    emit(\"close-toast\");\n}\nfunction selected(interactor: INotificationInteractor, event: Event) {\n    clickedOption(interactor, (event.target as HTMLSelectElement).selectedIndex, event);\n}\nfunction clickedOption(interactor: INotificationInteractor, index: number, event: Event) {\n    const selectedOption = interactor.options![index];\n    const close = () => decideClose(interactor);\n    if (!interactor.customClose) {\n        close();\n    } else {\n        event.stopPropagation();\n    }\n    interactor.onInteract({ selectedOption: { text: selectedOption.text, id: selectedOption.id, index }, close });\n}\nfunction clicked(interactor: INotificationInteractor, event: MouseEvent) {\n    const close = () => decideClose(interactor);\n    if (!interactor.customClose) {\n        close();\n    } else {\n        event.stopPropagation();\n    }\n    interactor.onInteract({ close });\n}\n\nfunction getIconClass() {\n    if (props.options.type == \"error\") return \"mdi mdi-close-circle\";\n    else if (props.options.type == \"info\") return \"mdi mdi-information\";\n    else if (props.options.type == \"success\") return \"mdi mdi-check-circle\";\n    else if (props.options.type == \"warning\") return \"mdi mdi-alert\";\n}\n</script>\n\n<template>\n    <div class=\"notification-container\">\n        <template v-if=\"showBlockShadow\">\n            <teleport to=\"body\">\n                <div class=\"blockShadow\" :style=\"{ zIndex: props.zindex }\"></div>\n            </teleport>\n        </template>\n        <h1 v-if=\"options.title\">{{ options.title }}</h1>\n        <i :class=\"iconClass + ' notification-icon'\" :style=\"options.type == 'info' ? 'color: white' : 'color: black'\" />\n        <div class=\"notification-text\">{{ options.text }}</div>\n        <template v-for=\"(interactor, index) in options.interactors\" :key=\"index\">\n            <template v-if=\"interactor.options\">\n                <div class=\"center\">\n                    <select v-if=\"interactor.optionsType == 'combo'\" @click.stop=\"\" @change=\"selected(interactor, $event)\">\n                        <option v-for=\"option in interactor.options\" :selected=\"option.selected\" :key=\"option.id\">{{ option.text }}</option>\n                    </select>\n                    <template v-else>\n                        <button\n                            class=\"action multi\"\n                            v-for=\"(option, index) in interactor.options\"\n                            :key=\"option.id\"\n                            @click=\"clickedOption(interactor, index, $event)\"\n                        >\n                            {{ option.text }}\n                        </button>\n                    </template>\n                </div>\n            </template>\n        </template>\n        <div class=\"close-text\">Close</div>\n    </div>\n</template>\n<style scoped>\n@import url(\"../../../styles/IDE3.css\");\n.blockShadow {\n    background-color: black;\n    top: 0px;\n    left: 0px;\n    position: fixed;\n    height: 100%;\n    width: 100%;\n    opacity: 0.7;\n}\n.center {\n    text-align: center;\n}\nh1 {\n    margin: 0px;\n}\n.action:hover {\n    border-color: black;\n}\n.action:active {\n    background-color: white;\n}\n.multi {\n    margin: 3px 5px 0px 5px;\n}\n.action {\n    height: 21px;\n    padding: 0px 6px;\n    border-radius: 5px;\n    color: #fff;\n    cursor: pointer;\n    border: thin solid currentColor;\n    background-color: transparent;\n}\n.notification-icon {\n    font-size: 36px;\n    margin-top: 16px;\n    margin-left: 16px;\n    margin-bottom: 16px;\n}\n.notification-icon::before {\n    line-height: normal;\n}\n.notification-text {\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) / var(--unnamed-line-spacing-21)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    text-align: left;\n    font: normal normal normal 16px/21px Roboto;\n    opacity: 1;\n\n    width: 259px;\n    margin-top: 24px;\n    margin-bottom: 24px;\n    margin-left: 12px;\n    word-wrap: break-word; \n}\n.close-text {\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-14) / var(--unnamed-line-spacing-19)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    text-align: left;\n    font: normal normal normal 14px/19px Roboto;\n    letter-spacing: 0px;\n    opacity: 1;\n    \n    cursor: pointer;\n    margin-left: 12px;\n    margin-right: 16px;\n}\n.notification-container {\n    display: flex;\n    align-items: center;\n}\n\n\n</style>\n\n<style>\n .Vue-Toastification__toast {\n    width: 388px;\n    box-shadow: 2px 2px 15px #00000066;\n    border-radius: 4px;\n    opacity: 1;\n    color: black;\n    padding: 0px;\n} \n.Vue-Toastification__toast--warning {\n    background: #f3ca40 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--success {\n    background: #29C660 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--error {\n    background: #FF6B6B 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--info {\n    color: white;\n    background: #151233 0% 0% no-repeat padding-box;\n}\n</style>"],"sourceRoot":""}]);
